.product {
	flex: 4;
	padding: 20px;
}

.productTitleContainer {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.productAddButton {
	width: 80px;
	border: none;
	padding: 5px;
	background-color: teal;
	border-radius: 5px;
	cursor: pointer;
	color: white;
	font-size: 16px;
}

.productContainer {
	display: flex;
	margin-top: 20px;
}

.productShow {
	flex: 1;
	padding: 20px;
	-webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.productUpdate {
	flex: 2;
	padding: 20px;
	-webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
	margin-left: 20px;
}

.productShowTop {
	display: flex;
	align-items: center;
}

.productShowImg {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	object-fit: cover;
}

.productShowTopTitle {
	display: flex;
	flex-direction: column;
	margin-left: 20px;
}

.productShowProductname {
	font-weight: 600;
}

.productShowProductTitle {
	font-weight: 300;
}

.productShowBottom {
	margin-top: 20px;
}

.productShowTitle {
	font-size: 14px;
	font-weight: 600;
	color: rgb(175, 170, 170);
}

.productShowInfo {
	display: flex;
	align-items: center;
	margin: 20px 0px;
	color: #444;
}

.productShowIcon {
	font-size: 16px !important;
}

.productShowInfoTitle {
	margin-left: 10px;
}

.productUpdateTitle {
	font-size: 24px;
	font-weight: 600;
}

.productUpdateForm {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
}

.productUpdateItem {
	display: flex;
	flex-direction: column;
	margin-top: 10px;
}

.productUpdateItem > label {
	margin-bottom: 5px;
	font-size: 14px;
}

.productUpdateInput {
	border: none;
	width: 250px;
	height: 30px;
	border-bottom: 1px solid gray;
}

.productUpdateRight {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.productUpdateUpload {
	display: flex;
	align-items: center;
}

.productUpdateImg {
	width: 100px;
	height: 100px;
	border-radius: 10px;
	object-fit: cover;
	margin-right: 20px;
}

.productUpdateIcon {
	cursor: pointer;
}

.productUpdateButton {
	border-radius: 5px;
	border: none;
	padding: 5px;
	cursor: pointer;
	background-color: darkblue;
	color: white;
	font-weight: 600;
}
